export class Paper {
  courseName: string = "健美操";
  courseId: number = 2;
  courseIndex: number = 50;
  sn: string = "3f21813df6670329";
}
export class Topic {
  constructor(t: object, keywords: string, courseId: number) {
    // @ts-ignore
    this.solutions = t.get("solutions");
    // @ts-ignore
    this.type = t.get("type");
    // @ts-ignore
    this.info = t.get("info");
    // @ts-ignore
    this.options = t.get("options");
    // @ts-ignore
    this.papers = t.get("papers");
    this.keywords = keywords
    const i = this.info, k = keywords
    this.infoArr = i.split(k).map((v, i) => {
      if (i > 0) {
        return [k, v]
      } else {
        return v
      }
    }).flat(2).map((v, i) => ({
      str: v,
      isKeywords: i % 2 == 1
    })).filter(v => v.str.length > 0)
    
    if (courseId > 0) {
      this.papers = this.papers.filter(v => v.courseId == courseId)
    }
    // if (courseIndex > 0) {
    //   this.papers = this.papers.filter(v => v.courseIndex == courseIndex)
    // }

    this.optionsArr = this.options.map((v, i) => ({
      str: v,
      right: this.solutions.includes(i),
    }))
    this.courseId = courseId
    // this.courseIndex = courseIndex
  }
  solutions: number[];
  type: number;
  info: string;
  options: string[];
  papers: Paper[];
  keywords: string;
  infoArr: object[];
  optionsArr: object[];
  courseId: number ;
  // courseIndex: number;
}
