
























































































import Vue from "vue";
import { Topic } from "@/class/Topic";
export default Vue.extend({
  props: {
    topic: {
      type: Topic,
      required: true
    },
    topicArr: {
      type: Array,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  methods: {
    close() {
      this.topicArr.splice(this.index, 1);
    }
  }
});
