import NProgress from "nprogress";

import { Notification } from "element-ui";
const log = window.console.log;
const subjectData = [
  "橄榄球",
  "健美操",
  "击剑",
  "篮球",
  "排球",
  "排舞",
  "乒乓球",
  "气排球",
  "散手",
  "射箭",
  "社交舞",
  "太极拳",
  "跆拳道",
  "网球",
  "校园定向",
  "艺术体操",
  "瑜伽",
  "羽毛球",
  "自卫防身",
  "足球"
];

const AV = require("leancloud-storage");
const { Query } = AV;
AV.init({
  appId: "iaMO7emahlMkiK4EDOkOeVDy-gzGzoHsz",
  appKey: "ot3R26MYi1ruSH1nv6Eq8Blf",
  serverURLs: "https://iamo7ema.lc-cn-n1-shared.com"
});

import { Paper, Topic } from "@/class/Topic";

export default {
  data() {
    return {
      subjects: [
        {
          value: 0,
          label: "无"
        },
        ...subjectData.map((v, i) => ({
          value: i + 1,
          label: v
        }))
      ],
      indexArr: [
        {
          value: 0,
          label: "无"
        },
        ...Array(50)
          .fill(0)
          .map((v, i) => ({
            value: i + 1,
            label: v
          }))
      ],
      types: ["判断题", "单选题", "多选题"].map((v, i) => ({
        value: i + 1,
        label: v
      })),
      params: {
        info: "",
        type: 1,
        courseId: 1,
        // courseIndex: 0
      },
      isLoading: false,
      publicPath: process.env.BASE_URL
    };
  },
  props: {
    topicArr: {
      type: Array,
      required: true
    }
  },
  methods: {
    search: function(event: Event) {
      NProgress.start();
      //  @ts-ignore
      const { type, courseId } = this.params;
      //  @ts-ignore
      const keywords = this.params.info;
      //  @ts-ignore
      this.isLoading = true;
      const query = new AV.Query("Topic");
      query.equalTo("type", type);
      query.contains("info", keywords);
      query.limit(10);
      const vm = this;

      try {
        query.find().then(function(topics1: object[]) {
          NProgress.done();
          //  @ts-ignore
          vm.isLoading = false;
          let topics: Topic[] = topics1.map(
            v => new Topic(v, keywords, courseId)
          );
          topics.forEach(v => {
            //  @ts-ignore
            vm.topicArr.unshift(Object.freeze(v));
          });
          //  @ts-ignore
          vm.$notify({
            title: "成功",
            message: `查询到${topics.length}条结果${topics.length==0?", 请尝试修改搜索项":""}`,
            type: "success"
          });
          //  @ts-ignore
          log(vm.topicArr);
        });
      } catch (error) {
        NProgress.done();
      }
      // let url =
      //   "https://raw.githubusercontent.com/lisonge/lisonge.gitHub.io/master/index.md";
      // url = "https://www.json-generator.com/api/json/get/cgnfvfmSJK";
      // // url = "http://songe.li/";
      // const funcx = async () => {
      //   try {
      //     const response = await axios.get(url, {
      //       params: this.params
      //     });
      //     log(response);
      //     this.isLoading = false;
      //     Notification.success({
      //       title: "搜索成功",
      //       message: "搜索到N张试卷"
      //     });
      //     // eslint-disable-next-line no-empty
      //   } catch (error) {
      //     this.isLoading = false;
      //     Notification.error({
      //       title: "搜索错误",
      //       message: error
      //     });
      //   }
      // };
      // funcx();
      // log(this.params);
    }
  }
};
